___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: rgb(12, 55, 184);
  --primary-color-darker: rgb(2, 16, 52);
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-position: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.hide {
  display: none;
}

.container {
  background-color: rgba(60, 89, 114, 0.541);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  padding: 2rem;
  width: 40%;
  color: #ffffff;
  border-radius: 1rem;
}

.logo{
    position: fixed;
    bottom: 0;
    top: 20px;
    display: flex;
    width: 15%;
    padding: 10px;
    margin: 15px;
}
.nameLogo{
  color: black;
}

/*FORM*/

.form h3 {
  margin-bottom: 1rem;
  text-align: center;
}

.form-input-container {
  display: flex;
}

.form input {
  padding: 0.8rem;
  width: 50%;
  border: none;
  flex: 1;
  border-radius: 4px;
}

.form button {
  padding: 0.8rem;
  min-width: 50px;
  margin-left: 8px;
  background-color: #5274da;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

span#warn {
  color: yellowgreen;
}

/*CLIMA*/

#weather-data {
  color: #fff;
  text-align: left;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

#weather-data h2 {
  margin-bottom: 0.6rem;
}

#weather-data h2 i {
  font-size: 1rem;
}

#weather-data span {
  font-size: 2rem;
}

#currentDate {
  font-size: 1.5rem;
}

#country {
  height: 15px;
}

#description-container {
  border-left: 1px solid #fff;
  margin: 0.6rem;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#description {
  font-weight: bold;
  text-transform: capitalize;
  font-size: 1.5rem;
}

#details-container {
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
}

#details-container #humidity {
  border-right: 1px solid #fff;
  margin: 0.6rem;
  padding: 0.6rem;
}

#details-container #temperature {
  border-right: 1px solid #fff;
  padding: 0.6rem;
  font-size: 2em;
}

.hide {
  display: none !important;
}

#social-links {
  text-align: right;
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  padding: 10px;
  margin: 15px;
}
#social-links a {
  padding: 10px;
  font-size: 24px;
  transition: background 0.2s;
}
#copy {
  color: var(--primary-color-darker);
  text-align: right;
  position: fixed;
  top: 35px;
  right: 0;
  display: flex;
  padding: 10px;
  margin: 15px 0px 5px;
}
